.teachers {
  position: relative;
  background: #f4f4f4;
  padding: 70px 0;
}

@media (max-width: 360px) {
  .teachers {
    padding: 40px 0;
  }
}

.teachers__title {
  margin-bottom: 10px;
}

@media (max-width: 360px) {
  .teachers__title {
    font-size: 30px !important;
  }
}

.teachers-header {
  margin-bottom: 40px;
}

.header-text {
  text-align: center;
}

.title {
  font-weight: 900;
  font-size: 35px;
  color: #000;
}

.text {
  font-size: 16px;
  color: #464646;
  font-weight: 400;
  line-height: 25px;
}

.teachers-slider {
  position: relative;
  padding-bottom: 10px;
  margin-bottom: 40px;
}

@media (max-width: 485px) {
  .teachers-slider {
    margin-bottom: 15px;
  }
}

.teachers-slider .swiper-button-next,
.teachers-slider .swiper-button-prev {
  position: absolute;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: #008de6;
  border-radius: 50px;
  z-index: 1000;
}

.teachers-slider .swiper-button-prev {
  left: -20px;
  top: 50%;
  transform: translateY(-50%);
}

.teachers-slider .swiper-button-next {
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.teachers-slider .swiper-wrapper {
  margin-bottom: 50px;
}

@media (max-width: 485px) {
  .teachers-slider .swiper-wrapper {
    margin-bottom: 20px;
  }
}

.swiper-pagination-bullet {
  cursor: pointer;
  display: block;
  width: 22px;
  height: 22px;
  border-radius: 50px;
  background: #cacaca;
  margin: 0 8px;
}

.swiper-pagination-bullet-active {
  border: 5px solid #008de6;
  background: #fff;
  transform: scale(1.3);
}

.left-arrow,
.right-arrow {
  width: 10px;
  height: 18px;
}

.teachers-slide {
  overflow: hidden;
  position: relative;
  text-align: center;
  border-radius: 25px;
  background: #fff;
  border: 2px solid #008de6;
  padding-bottom: 25px;
}

.teachers-slide__photo {
  margin-bottom: 15px;
}

.teachers-slide__photo img {
  width: 100%;
}

.teachers-slide__language {
  font-weight: 700;
  font-size: 19px;
  color: #000;
}

.teachers-slide__quantity {
  color: #464646;
  font-size: 16px;
  font-weight: 400;
}

.country-flag {
  position: absolute;
  right: 4px;
  bottom: 34px;
}

@media (max-width: 370px) {
  .country-flag {
    width: 70px;
    bottom: 43px;
  }
}

.check-level {
  border-radius: 50px;
  max-width: 347px;
  width: 100%;
  display: block;
  background: #a11d59;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 16px;
  color: #fff;
  margin: 0px auto;
  transition: all 0.2s;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.check-level:hover {
  background: #981b54;
}

.ask-img {
  position: absolute;
  right: 20px;
